<template>
        <v-navigation-drawer
            v-model="drawer"
            width="290"
            app

            :mini-variant.sync="mini"
            mini-variant-width="68"
            
            enable-resize-watcher
            floating
            permanent
            hide-overlay
            class="right-shadow qiong-pt-20"
        >

            <!-- 用户组建 -->
            <menu-msg-widget v-if="this.$store.state.platform_data" :id="this.$store.state.platform_data.userID" :need_avatar="false"></menu-msg-widget>

            <!-- 按键列表 -->
            <lad-lister class="qiong-pt-15 mt-2"></lad-lister>

            <v-spacer></v-spacer>

            <!-- 同步程序 -->
            <async-top-widget></async-top-widget>

        </v-navigation-drawer>
</template>

<script>
import QiongGradientLine from '../Qiong/Ui/QiongGradientLine.vue'
import LevelImageViewing from '../Viewing/LevelImageViewing.vue'
import LadBonus from './Inner/LadBonus.vue'
import LadLister from './LadLister.vue'
import LadMsg from './Inner/LadMsg.vue'
import MenuMsgWidget from './MenuMsgWidget.vue'
import AsyncTopWidget from './TopContent/AsyncTopWidget.vue'
    export default {
        name: '',
        inject: [ 'reload' ],
        components: {

                LadLister,
                QiongGradientLine,
                LadMsg,
                LadBonus,
                LevelImageViewing,
                MenuMsgWidget,
                AsyncTopWidget
        },
        data() {
            return {
                drawer: false,
                mini: false,
            }
        },
        mounted() {

        },
        methods: {
            changeF() {
                this.drawer = !this.drawer
            },
        }
    }
</script>

<style lang="sass" scoped>
    .right-shadow
        // box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.14)

    .last-nav
        position: absolute
        bottom: 0
        width: 100%
</style>


